@import '~node_modules/modern-normalize/modern-normalize.css';
.gallery{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 10px;
   
}
.gallery__item{
        background-color: white;
        color: black;
        padding: 10px;
        border-radius: 4px;
        cursor: pointer;
}
.info-wrap {
    pointer-events: none;
}

.gallery__option{
    background-color: gray;
  
   display: flex;
   align-items: center;
   justify-content: space-around;
   
}
.libary__options{
    display: flex;
     align-items: center;
    justify-content: center;
  
}

